.TodoCounter {
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
  }
.TodoSearch {
    background: black;
    border-radius: 2px;
    border: 2px solid red;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: white;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .TodoSearch::-webkit-input-placeholder {
    color: white;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch::placeholder {
    color: white;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoSearch:focus {
    outline-Scolor: #d4d806;
  }
ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
  }
.TodoItem {
    background-color: #868585;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(220, 220, 221, 0.15);
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.TodoItem-p--complete {
    text-decoration: line-through;
    color: gold;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
}

.Icon-check {
    position: absolute;
    left: 12px;
}
.Icon-check--active {
    color: gold;
}

.Icon-delete {
    position: absolute;
    top: -24px;
    right: 0;
}
.Icon-delete:hover {
    color: black;
}
form {
    width: 90%;
    max-width: 300px;
    background-color: rgb(119, 11, 11);
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: white;
    margin-bottom: 26px;
}

textarea {
    background-color: white;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: black;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

textarea::-webkit-input-placeholder {
    color: blue;
    font-family: 'Montserrat';
    font-weight: 400;
}

textarea::placeholder {
    color: blue;
    font-family: 'Montserrat';
    font-weight: 400;
}

textarea:focus {
    outline-color: gold;
}

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
}

.TodoForm-button--add {
    background: gold;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button--cancel {
    background: red;
}

.CreateTodoButton {
    background-color: black;
    box-shadow: 0px 5px 25px gold;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;

    -webkit-transform: rotate(0);

            transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
}

.CreateTodoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
    background-color: white;
    color: black;
}

.LoadingTodo-container {
    position: relative;
    width: 100%;
    min-height: 60px;
    border-radius: 20px;
    margin: 10px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.LoadingTodo-text {
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    color: white;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
    position: absolute;
    font-size: 24px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
}

.LoadingTodo-completeIcon {
    left: 0;
}

.LoadingTodo-deleteIcon {
    position: absolute;
    right: 0;
}

.LoadingTodo-container {
    background: #868585;
    background-size: 400% 400%;
    -webkit-animation: loadingAnimation 3s ease-in-out infinite;
            animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes loadingAnimation {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

body {
  margin: 0;
  padding: 0;
  background: rgb(59, 15, 15);
  color: white;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}
